@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
  margin: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: Russo One;
  background-color: grey;
}

button {
  font-size: 1rem;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#drum-machine {
  display: flex;
  align-items: center;
  justify-contnent: center;
  text-align: center;
  background-color: #b3b3b3;
  border: 4px orange solid;
  width: 660px;
}

.pad-container {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(3, 80px);
  grid-gap: 10px;
  padding: 30px;
}

.pad-container button {
  background-color: grey;
  box-shadow: black 3px 3px 5px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

button {
   font-family: Russo One;
}

.function-button {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 30px;
  margin-left: auto;
}

p {
  text-align: center;
  margin: 0;
}

.button {
  padding: 10px;
}

.fb {
  height: 25px;
  width: 60px;
  border-radius: 0;
  border: none;
  background-color: black;
}

#child {
  background-color: blue;
  width: 50%;
  height: 80%;
  align-items: center;
  display: flex;
  cursor: pointer;
}

#display {
  background-color: grey;
  width: 200px;
  height: 50px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

